import React from 'react';
import { useState } from 'react';
import './App.css';
import Budget  from './Components/Budget/Budget';
import AddExpense from './Components/UI/AddExpense';
import Expenses from './Components/Budget/Expenses'

let mystorage = window.localStorage;



let initExpenses;


if (mystorage.getItem('expenseList')) {
    initExpenses = JSON.parse(mystorage.getItem('expenseList'));
}
else {
    initExpenses = [];
}



const getTotal = (a,b) => {
  return(a+b);
}




function App() {

  const [expenses, setExpenses]=useState(initExpenses);

  let va = expenses.map((ev) => {
    if(ev.isPaid === false) {
      return (ev.value)
    }
    else return 0
    
    });
    let tv = va.reduce(getTotal,0);
    


  const [expenseTotal, setExpenseTotal]=useState(tv)
  const [clicked, setClicked]=useState(false)
    
  const handleformsub = (t,v) => {
    setClicked(false)
    setExpenses((prevValue) => {
        let e = [...prevValue, {id:Math.random().toString(), title: t, value: parseFloat(v), isPaid: false}] 
        mystorage.setItem('expenseList', JSON.stringify(e))

        console.log('newE'+e)

        console.log(expenseTotal)

        let newVa = e.map((e) => {
          if(e.isPaid === false) return(e.value)
          else return 0;
        })
        let newT = newVa.reduce(getTotal,0);
        
        console.log(newT);
        setExpenseTotal(newT)

      return (
        [...prevValue, {id:Math.random().toString(), title: t, value: parseFloat(v), isPaid: false}]
      )
    })
  }


  const handleDelete = (ukey) => {
    let modArray = expenses.filter(ex => ex.id !== ukey)
    setExpenses(modArray)
    mystorage.setItem('expenseList', JSON.stringify(modArray))

    let newVa = modArray.map((e) => {
      if(e.isPaid === false) return(e.value)
      else return 0
      
    })
    let newT = newVa.reduce(getTotal,0);
    
    console.log('valueDeleted',newT);
    setExpenseTotal(newT)
  }

 




  const handlePaidChange = (ukey) => {
    
    let exp = expenses.filter(e => e.id === ukey);
    exp = exp[0];
    

    let newExpArray = expenses.map((e) => {
      if(e.id === ukey) {
        e.isPaid = !e.isPaid;
        return(e)
      }
      else {
        return(e)
      }
    })

    let v;

    exp.isPaid === true ? v = expenseTotal - exp.value : v = expenseTotal + exp.value

    console.log(v,expenseTotal,exp);


    setExpenses(newExpArray)
    mystorage.setItem('expenseList', JSON.stringify(newExpArray))
    setExpenseTotal(v)
  }

  const resetClicked = (isClicked) => {
    setClicked(isClicked)
    
  }


  return (
    <div style={{paddingBottom:'5rem'}}>
      <Budget t={expenseTotal} />
      <AddExpense onSub={handleformsub} clicked={clicked} resetClicked={resetClicked} />
      <Expenses expenses={expenses} onDelete={handleDelete} onPaidChange={handlePaidChange}/>
    </div>
  )
}

export default App;
