import React from "react";
import Expense from "./Expense";
import Card from "../UI/Card";


const Expenses = (props) => {
    const debug = () => {
        console.log(props.expenses)
    }
    debug();

    const handleDelete = (ukey) => {
        props.onDelete(ukey)
    }

    


    const handlePaidStatus = (ukey) => {
        props.onPaidChange(ukey)
    }

    return (
        <Card>
            {props.expenses.map((e) => {
                console.log(e)
                return (<Expense key={e.id} value={e.value} title ={e.title} isPaid={e.isPaid} ukey = {e.id}  onDelete={handleDelete}  apaid={e.isPaid} onPaidChange={handlePaidStatus}/>)
            })}
        </Card>
    )
}

export default Expenses