import React, {useEffect, useState} from "react";
import './AddExpense.css';
import Card from "./Card";

const AddExpense = (props) => {

    const [titleValue, setTitleValue]=useState(props.title);
    const [valueValue, setValueValue]=useState('');
    const [buttonClicked, setButtonClicked]=useState(props.clicked);

    useEffect(()=> {
        setButtonClicked(props.clicked)
        setTitleValue(props.title)
        setValueValue(props.value)

    },[props.clicked, props.title, props.value])


    const handleTitleChange = (event) => {
        setTitleValue(event.target.value);

    }
    const handleValueChange = (event) => {
        setValueValue(event.target.value);
        
    }

    const handleSubmit = (event)  => {
        event.preventDefault();
        props.onSub(titleValue, valueValue);
        setTitleValue('');
        setValueValue('');
        setButtonClicked(!buttonClicked)
    
        

    }

    const handleButtonClick = () => {
        props.resetClicked(!buttonClicked);
        setButtonClicked(!buttonClicked);
        

    }
    

    return (
        <>
        <div style={{textAlign:'right', width:'80%', maxWidth:'600px', margin: '5px auto', padding: '8px 0px'}}>
            <button onClick={handleButtonClick}>{buttonClicked === false ? 'Add Expense' : 'Close'}</button>
        </div>
        {buttonClicked === true &&
            <Card >
                <div style={{borderBottom:'3px solid #6F1E51', marginBottom:'1rem'}}>
                    <form onSubmit={handleSubmit}>
                        <div><label>Title</label></div>
                        <div><input onChange={handleTitleChange} value={titleValue}></input></div>
                        <div><label>Value</label></div>
                        <div><input onChange={handleValueChange} value={valueValue}></input></div>
                        <div><input type='submit'></input></div>
                    </form>
                </div>
            </Card>
        }
        </>
    )
}

export default AddExpense;
