import React, { useEffect, useReducer, useState } from "react";
import Card from "../UI/Card";
import './Budget.css' 

const mystorage = window.localStorage;
let initBalance = 0;

if(mystorage.getItem('dataBalance')) {
    initBalance = mystorage.getItem('dataBalance')
}

const daysuntilpay = () => {
  var date = new Date();
  var time = new Date(date.getTime());
  // get the date of 28th.
  let payday = new Date();
  let pd;
  payday.setDate(28);
  let day = payday.getDay();
  if (day === 0 )  {
    pd = 26;
  }
  else if (day === 6) {
    pd = 27
  }
  else {
    pd = 28
  }
  
  time.setMonth(date.getMonth() + 1);
  time.setDate(0);
  let days = time.getDate() > date.getDate() ? time.getDate() - date.getDate() : 0;
  
  if(date.getDate() >= pd) {
    return (days + pd)
  }
  else {
    return (pd - date.getDate())
  }
}


const Budget = (props) => {

    const [clicked, setCilcekd]=useState(false)
    let daysrem = daysuntilpay();


    const budgetReducer = (state, action)  => {
      if(action.type === 'FIELD_BLUR') {
        let b=action.value;
        let r = b - state.expensesTotal;
        let pd = r / daysrem;
    
        return ({balance: b, remainingBal: r, perday:pd, expensesTotal: state.expensesTotal})
        
      }
      if(action.type === 'EXPENSE_CHANGE') {
        let r = state.balance - action.value;
        let pd = r / daysrem;
        return ({balance:state.balance, remainingBal:r, perday:pd, expensesTotal:action.value})
      }
      return(state)
    }

    const initBudget = () => {
      let r = initBalance - props.t;
      let pd = r / daysrem;
      return ({
        balance: initBalance,
        remainingBal: r,
        perday: pd,
        expensesTotal: props.t

      })
    }
    
    const [budgetState, dispatchBudget]=useReducer(budgetReducer,{balance: initBalance, remainingBal: 0, perday:0, expensesTotal: props.t},initBudget)

    
    useEffect(() => {
      dispatchBudget({type:'EXPENSE_CHANGE',value:props.t})
    },[props.t])


    const handleClick = () => {
        setCilcekd(!clicked);
    }

    const handleBlur = (event) => {
        let updatedBalance = event.target.value;
        mystorage.setItem('dataBalance', updatedBalance)
        dispatchBudget({type: 'FIELD_BLUR', value:event.target.value})
        setCilcekd(!clicked);
    }

    return (
        <Card>
            {clicked === false ? <h1 className='balance' onClick={handleClick}>£{budgetState.balance}</h1> : <div><input className="balinput" defaultValue={budgetState.balance} onBlur={handleBlur} autoFocus></input></div>}
            <h2 className='remaining'>£{budgetState.remainingBal.toFixed(2)}</h2>
            <h3 className='perday'>£{budgetState.perday.toFixed(2)} /day {daysrem} days until pay</h3>
        </Card>
    )
}

export default Budget