import React, {useState} from "react";
import './Expense.css';



const Expense = (props) => {

    const [paidstatus,setPaidStatus]=useState(props.apaid);

    

    const handleDelete = () => {
        props.onDelete(props.ukey)
    }

    

    const paidClickHandler = () => {
        setPaidStatus((prevState) => {
            return (
                !prevState
            )
        });
 
        
        props.onPaidChange(props.ukey)
    }

    return (
        
        <div className='card' style={{flexWrap:'wrap'}}>
            <div style={{width:'100%', display:'flex'}}>
                <div>
                    {props.title}
                </div>
                <div>
                    £{props.value.toFixed(2)}
                </div>
                
                <div className={paidstatus === true ? 'paid' : 'notpaid'} onClick={paidClickHandler} >{paidstatus === true ? 'Paid' : 'To Pay'}</div>
            </div>
            <div style={{width:'100%', flexShrink:'0', padding: '1rem'}}>
                <button onClick={handleDelete}>delete</button>
               
            </div>
        </div>
    )
}

export default Expense